document.querySelectorAll(".email-form").forEach((form) => {
    let isSubmitting = false;

    const emailInput = form.querySelector('input[name="email"]');
    const nameInput = form.querySelector('input[name="name"]');

    const emailError = form.querySelector(".email-error");
    const nameError = form.querySelector(".name-error");

    // Live Ð¾Ñ‡Ð¸Ñ‰ÐµÐ½Ð½Ñ Ð¿Ð¾Ð¼Ð¸Ð»Ð¾Ðº
    emailInput.addEventListener("input", () => {
        emailInput.classList.remove("invalid");
        emailError.textContent = "";
    });

    if (nameInput) {
        nameInput.addEventListener("input", () => {
            nameInput.classList.remove("invalid");
            nameError.textContent = "";
        });
    }

    form.addEventListener("submit", function (e) {
        e.preventDefault();

        if (isSubmitting) return; // ðŸ›‘ Ð·Ð°Ñ…Ð¸ÑÑ‚ Ð²Ñ–Ð´ Ð¿Ð¾Ð²Ñ‚Ð¾Ñ€Ð½Ð¾Ñ— Ð²Ñ–Ð´Ð¿Ñ€Ð°Ð²ÐºÐ¸

        const email = emailInput.value.trim();
        const name = nameInput?.value.trim();
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

        let isValid = true;

        // Email Ð²Ð°Ð»Ñ–Ð´Ð°Ñ†Ñ–Ñ
        if (!email) {
            emailError.textContent = "ÐŸÐ¾Ð»Ðµ email Ð¾Ð±Ð¾Ð²â€™ÑÐ·ÐºÐ¾Ð²Ðµ";
            emailInput.classList.add("invalid");
            isValid = false;
        } else if (!emailRegex.test(email)) {
            emailError.textContent = "Ð’Ð²ÐµÐ´Ñ–Ñ‚ÑŒ ÐºÐ¾Ñ€ÐµÐºÑ‚Ð½Ð¸Ð¹ email";
            emailInput.classList.add("invalid");
            isValid = false;
        }

        // Name Ð²Ð°Ð»Ñ–Ð´Ð°Ñ†Ñ–Ñ
        if (nameInput && !name) {
            nameError.textContent = "ÐŸÐ¾Ð»Ðµ Ñ–Ð¼â€™Ñ Ð¾Ð±Ð¾Ð²â€™ÑÐ·ÐºÐ¾Ð²Ðµ";
            nameInput.classList.add("invalid");
            isValid = false;
        }

        if (!isValid) return;

        // ðŸŸ¡ Ð£ÑÑ‚Ð°Ð½Ð¾Ð²Ð»ÑŽÑ”Ð¼Ð¾ Ð¿Ñ€Ð°Ð¿Ð¾Ñ€ÐµÑ†ÑŒ "Ð·Ð°Ð¿Ð¸Ñ‚ Ñ‚Ñ€Ð¸Ð²Ð°Ñ”"
        isSubmitting = true;

        const data = { email };
        data.source = "audit-ua";

        if (nameInput) data.name = name;

        fetch("https://remrkt.me/api/leads", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(data),
        })
            .then((res) => {
                if (!res.ok) throw new Error("ÐŸÐ¾Ð¼Ð¸Ð»ÐºÐ° ÑÐµÑ€Ð²ÐµÑ€Ð°");
                return res.json();
            })
            .then(() => {
                alert("Ð¤Ð¾Ñ€Ð¼Ð° ÑƒÑÐ¿Ñ–ÑˆÐ½Ð¾ Ð½Ð°Ð´Ñ–ÑÐ»Ð°Ð½Ð°!");
                form.reset();
                emailInput.classList.remove("invalid");
                emailError.textContent = "";
                if (nameInput) {
                    nameInput.classList.remove("invalid");
                    nameError.textContent = "";
                }

                // Ð—Ð°ÐºÑ€Ð¸Ñ‚Ð¸ Ð¼Ð¾Ð´Ð°Ð»ÐºÑƒ, ÑÐºÑ‰Ð¾ Ñ„Ð¾Ñ€Ð¼Ð° Ð² Ð½Ñ–Ð¹
                const modal = form.closest(".modal");
                if (modal) modal.classList.remove("active");
            })
            .catch((err) => {
                alert("ÐŸÐ¾Ð¼Ð¸Ð»ÐºÐ° Ð¿Ñ€Ð¸ Ð²Ñ–Ð´Ð¿Ñ€Ð°Ð²Ñ†Ñ–.");
                console.error(err);
            })
            .finally(() => {
                // ðŸ”“ Ð”Ð¾Ð·Ð²Ð¾Ð»Ð¸Ñ‚Ð¸ Ð½Ð°ÑÑ‚ÑƒÐ¿Ð½Ñƒ Ð²Ñ–Ð´Ð¿Ñ€Ð°Ð²ÐºÑƒ
                isSubmitting = false;
            });
    });
});
